*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.navbar{
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 75px;
    color: white;
    background-color: blue;
}
.nav-links{
    display: flex;
    justify-content: flex-end;
    list-style: none;
    width: 75%;
}
.home,.products,.signup,.about{
text-decoration: none;
color: white;
font-size: 20px;
padding: 10px 20px;
margin: 0 10px;
cursor: pointer;
}
.signup{
    border: 1px solid yellow;
    border-radius: 50px;
    background-color: rgb(216, 33, 240);

}
.mob-menu-icon{
    display: none;
}
@media  screen and(max-width :780px) {
    .logo{
        display: flex;
        position: absolute;
        top: 15px;
        left: 35px;
    }

.nav-links{
    display: none;
}
.nav-links-mob{
    position: absolute;
    display: block;
    background-color: aqua;
    left: 0px;
    top: 75px;
    transition: all 0.5s ease-out;
    width: 100%;
}
.home,.products,.signup,.about{
    color: white;
    text-align: center;
    padding: 32px;
    width: 100%;
    transition: all 0.5s ease;
}
.signup{
    border: none;
}
.mob-menu-icon{
    display: block;
    position: absolute;
    font-size: 30px;
    color: white;
    background-color: blue;
    outline: none;
    top: 15px;
    right: 25px;
    border: none;
}
}
